import Item from './Item/Item';
import classes from './Items.module.css';

const Items = props => {
    const itemsData = [
    ]

    if(props.isInfoFilled) {
        itemsData.push({ name: 'reset' })
        if(props.pickedTemplate !== null) {
            itemsData.push({ name: 'choose' })
            itemsData.push({ name: 'print' })  
        }
    }

    return (
        <div className={classes.items} >                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                    
            { itemsData.map(data => <Item {...data} 
                key={data.name} 
                onClick={() => props.clickHandler(data.name)}
                hamburgerOpen={props.hamburgerOpen} />) }
        </div>
    )
}

export default Items;